@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://cdn.jsdelivr.net/npm/xeicon@2.3.3/xeicon.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}.cf::after{content:"";clear:both;display:block}body{font-family:'Nanum Gothic', sans-serif}ul{list-style:none}a{text-decoration:none;color:#222}

.appBody{position:fixed;top:0;bottom:0;left:0;right:0;background:url(../../static/media/fractals-environment-1728589_1920.8e24ea96.jpg) center/cover;font-family:'Righteous', cursive}.appBody .filter{position:fixed;top:0;bottom:0;left:0;right:0;background:rgba(0,0,0,0.5)}.appBody .filter .loading{background:#000;position:fixed;top:0;bottom:0;left:0;right:0;color:#995;justify-content:flex-end;align-items:flex-end;display:flex;visibility:hidden;opacity:0;transition:all .5s;z-index:10}.appBody .filter .loading h2{font-size:80px;padding:50px}.appBody .filter .loading.on{visibility:visible;opacity:1}

.tableWindow{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);width:500px;height:600px;border:3px solid #955;border-radius:25px;background:rgba(0,0,0,0.5);padding:20px}.tableWindow h2{color:#995;font-size:40px;margin-bottom:30px;text-align:center}.tableWindow table{margin-left:80px;color:#bbb;font-size:30px}.tableWindow table tr td:nth-of-type(1){width:8%}.tableWindow table tr td:nth-of-type(2){width:16%}.tableWindow table tr td:nth-of-type(3){width:24%}.tableWindow button{width:100px;height:50px;line-height:50px;text-align:center;margin:40px auto;display:block;font-size:20px;font-family:'Righteous', cursive;background:#333;color:#aaa}.tableWindow button:hover{background:#555;color:#ccc}

